/* General styles */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f4;
}

.app {
  max-width: 900px;
  margin: 50px auto;
  padding: 20px;
  background-color: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h1 {
  text-align: center;
  margin-bottom: 30px;
}

/* Tabs */
.tabs {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.tab {
  background-color: #f0f0f0;
  padding: 10px 20px;
  border: 1px solid #ddd;
  margin-right: 10px;
  cursor: pointer;
  font-weight: bold;
}

.tab.active {
  background-color: #007bff;
  color: white;
}

/* Section styling */
.upload-section {
  margin-bottom: 30px;
}

.upload-section h2 {
  margin-bottom: 20px;
}

.upload-section input[type="file"] {
  display: block;
  margin-bottom: 20px;
}

.upload-section label {
  display: block;
  margin-bottom: 10px;
}

.upload-section input[type="range"], .upload-section input[type="number"] {
  width: 100%;
  margin-bottom: 20px;
}

button {
  background-color: #007bff;
  color: white;
  padding: 10px 20px;
  border: none;
  cursor: pointer;
  font-size: 16px;
  margin-top: 10px;
}

button:hover {
  background-color: #0056b3;
}

/* Image and Video Outputs */
img, video {
  display: block;
  margin-top: 20px;
  max-width: 100%;
}
